import newsItem from '../../components/news-item/news-item.vue'
export default {
	data() {
		return {
			newsList: []
		}
	},
	methods: {
		async getNews () {
			const res = await this.$myRuquest({
				url: '/api/getnewslist'
			})
			this.newsList = res.data.message
		},
		goDetail (id) {
			uni.navigateTo({
				url: '/pages/news-detail/news-detail?id='+id
			})
		}
	},
	components: {"news-item":newsItem},
	onLoad () {
		this.getNews()
	}
}
